*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.skills{
    height: 84vh;
    background-color: rgb(205, 255, 247);
}
.ski{
    width: 38%;
    height: 58%;
    margin: 0 5rem;
    /* background-color: red; */
}
.skills-desc{
    width: 35%;
    margin-right: 5rem;
    /* background-color: green; */
}
.percentbar{
    background-color: #ffffff;
    width: 23rem;
    height: 8px;
}
.frontendpercent{
    background-color: rgb(255, 126, 66);
    width: 75%;
    height: 8px;
    animation: fp 1.6s ;
}
@keyframes fp {
    0%{
        background-color: rgb(255, 126, 66);
        width: 0%;
        
    }
    100%{
        background-color: rgb(255, 126, 66);
        width: 75%;
    }
}
.backendpercent{
    background-color: rgb(255, 126, 66);
    width: 30%;
    height: 8px;
    animation: bp 1.6s ;
}
@keyframes bp {
    0%{
        background-color: rgb(255, 126, 66);
        width: 0%;
        
    }
    100%{
        background-color: rgb(255, 126, 66);
        width: 30%;
    }
}
.reactpercent{
    background-color: rgb(255, 126, 66);
    width: 50%;
    height: 8px;
    animation: rp 1.6s ;
}
@keyframes rp {
    0%{
        background-color: rgb(255, 126, 66);
        width: 0%;
        
    }
    100%{
        background-color: rgb(255, 126, 66);
        width: 50%;
    }
}